<template>
	<div id="statcenter-goods">
		<public-header title="热门商品"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group van-hairline--bottom">
					<div class="tab-item " :class="{active: days == 0}" @click="onToggleDays(0)">今天</div>
					<div class="tab-item" :class="{active: days == 7}"  @click="onToggleDays(7)">近7天</div>
					<div class="tab-item" :class="{active: days == 30}"  @click="onToggleDays(30)">近30天</div>
					<div class="tab-item" :class="{active: days == -1}"  @click="onToggleDays(-1)">自定义</div>
				</div>
				<div class="diy-time" v-if="days == -1">
					<span @click="onTogglePicker('start')">
						<input v-model="stat_day.start" type="text"  disabled>
					</span>
					<span class="padding-15-lr c-gray">~</span>
					<span @click="onTogglePicker('end')">
						<input v-model="stat_day.end" type="text" disabled>
					</span>
					<van-button size="mini" class="bg-info margin-10-l" :disabled="!stat_day.start || !stat_day.end" @click="onLoad(true)">确定</van-button>

				</div>
			</div>
			<div class="rank-list" :class="{'is-diy': days == -1}">
				<van-row class="font-15 c-gray flex-lr">
					<van-col span="3">排名</van-col>
					<van-col span="7">商品名称</van-col>
					<van-col span="7">
						<span @click="onOrderBy('total_goods_num')" :class="{'c-danger': orderby == 'total_goods_num'}">
							销售量<i class="icon icon-order1 font-16"></i>
						</span>
					</van-col>
					<van-col span="7">
						<span @click="onOrderBy('total_goods_price')" :class="{'c-danger': orderby == 'total_goods_price'}">
							销售额<i class="icon icon-order1 font-16"></i>
						</span>
					</van-col>
				</van-row>
				<van-row class="flex-lr" v-for="(item, index) in records" :key="index" v-if="records.length > 0">
					<van-col span="3">{{index+1}}</van-col>
					<van-col span="7">{{item.goods_title}}</van-col>
					<van-col span="7">{{item.total_goods_num}}</van-col>
					<van-col span="7">¥{{item.total_goods_price}}</van-col>
				</van-row>
			</div>
		</div>
		<van-popup v-model="timePicker" position="bottom">
			<van-datetime-picker
				v-model="currentDate"
				type="date"
				:min-date="minDate"
				:max-date="maxDate"
				@cancel="onTogglePicker"
				@confirm="onConfirmTime"
			/>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				timePicker: false,
				minDate: new Date(2014, 12, 1),
				maxDate: new Date(),
				currentDate: new Date(),
				timeType: '',
				days: 0,
				stat_day: {
					start: this.util.formatDate( new Date(), 'date'),
					end: this.util.formatDate( new Date(), 'date')
				},
				orderby: 'total_goods_price',
				records: [],
				showPreLoading: true
			}
		},
		methods: {
			onLoad(force) {
				var that = this;
				if(force) {
					this.records = [];
				}
				getData({
					vue: that,
					url: 'manage/statcenter/goods',
					data: {
						days: that.days,
						stat_day: that.stat_day,
						orderby: that.orderby
					},
					success: (res) => {
						that.records = [...that.records, ...res.records];
					}
				})
			},
			onTogglePicker(type) {
				if(type) {
					this.timeType = type;
				}
				this.timePicker = !this.timePicker;
			},
			onConfirmTime(value) {
				value = this.util.formatDate(value, 'date');
				this.stat_day[this.timeType] = value;
				this.onTogglePicker();
			},
			onToggleDays(value) {
				if(value == this.days) {
					return;
				}
				this.days = value;
				if(this.days != -1) {
					this.onLoad(true);
				}
			},
			onOrderBy(value) {
				if(value == this.orderby) {
					return;
				}
				this.orderby = value;
				this.onLoad(true);
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#statcenter-goods .diy-time{
		padding: 10px 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		background-color: #fff;
	}
	#statcenter-goods .diy-time input{
		width: 100px;
		font-size: 14px;
		border: 1px solid #ccc;
		border-radius: 3px;
		background-color: #fff;
		text-align: center;
		padding: 3px 0;
	}
	#statcenter-goods .rank-list{
		background-color: #fff;
		padding: 0px;
		text-align: center;
		font-size: 14px;
		position: absolute;
		top: 45px;
		right: 0;
		bottom: 0;
		left: 0;
		overflow-y: scroll;
		overflow-x: hidden;
	}
	#statcenter-goods .rank-list.is-diy{
		top: 102px;
	}
	#statcenter-goods .rank-list .van-row:nth-child(odd){
		background-color: #f5f5f5;
	}
	#statcenter-goods .rank-list .van-row .van-col{
		border-right: 1px solid #fefefe;
		padding: 10px 5px;
	}
	#statcenter-goods .rank-list .van-row .van-col:last-child{
		border: 0px;
	}
	#statcenter-goods .rank-list .van-row:first-child .van-col{
		padding: 10px 0;
	}
	#statcenter-goods .diy-time .van-button--mini{
		height: 26px;
	}
</style>


